.Footer {
    text-align: center;
    color: #c8c8c8;
    background-color: #292b30;
    padding: 3rem 10%;
    box-shadow: 0 0 1px 0 rgba(0, 0, 0, .5);
}

@media(min-width: 376px) {
    .Footer__Social {
        padding: 0 30%;
    }
}

.Footer__Social {
    padding: 0 10%;
}

.Footer__Social a {
    color: #c8c8c8;
}

.Footer__Social div i {
    font-size: 1.5rem;
    width: 100%;
}

.Footer__Social div:hover i {
    color: white;
}

.Footer__Copyright__Txt {
    font-size: 1rem;
    color: #c8c8c8;
}

.Footer__Copyright__Link:hover {
    color: white;
}

.Footer__Hr {
    margin: 2rem;
}